@import 'sanitize.css/sanitize.css';
@import 'sanitize.css/forms.css';
@import 'sanitize.css/assets.css';
@import 'sanitize.css/typography.css';
:root {
  --color-black: #000000;
  --color-white: #ffffff;
  --black: #000000;
  --white: #ffffff;
  --purple: #6028ff;
  --grey-dark: #282730;
  --purple-light: #d4d5ff;
  --grey-light: #a4a0a0;
  --font-base: var(--font-sans);
  --font-sans: Inter, -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Ubuntu", "Droid Sans", "Helvetica Neue", Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";
  --font-unbounded: Unbounded, -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Ubuntu", "Droid Sans", "Helvetica Neue", Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";
  --font-size-root: 100%;
  --font-size-base: 1rem;
  --font-weight-base: 400;
  --line-height-base: 1.25;
}

:root {
  font-size: var(--font-size-root);
}

html,
body {
  height: 100%;
}

html {
  min-width: 320px;
  min-height: 100%;
  font-size: var(--font-size-root);
  font-family: sans-serif;
  line-height: normal;
  -webkit-tap-highlight-color: rgb(var(--color-black) 0);
  -webkit-text-size-adjust: 100%;
  scroll-behavior: smooth;
}

body {
  margin: 0;
  color: var(--body-color);
  font-weight: var(--font-weight-base);
  font-size: var(--font-size-base);
  font-family: var(--font-base);
  line-height: var(--line-height-base);
  background-color: var(--body-bg);
}

svg {
  display: block;
}

[tabindex="-1"]:focus:not(:focus-visible) {
  outline: 0;
}

[hidden] {
  display: none;
}

@font-face {
  font-weight: 300;
  font-family: Inter;
  font-style: normal;
  font-display: swap;
  src: url("/{{fonts}}/inter/Inter-Light.woff2") format("woff2");
}
@font-face {
  font-weight: 400;
  font-family: Inter;
  font-style: normal;
  font-display: swap;
  src: url("/{{fonts}}/inter/Inter-Regular.woff2") format("woff2");
}
@font-face {
  font-weight: 500;
  font-family: Inter;
  font-style: normal;
  font-display: swap;
  src: url("/{{fonts}}/inter/Inter-Medium.woff2") format("woff2");
}
@font-face {
  font-weight: 700;
  font-family: Inter;
  font-style: normal;
  font-display: swap;
  src: url("/{{fonts}}/inter/Inter-Bold.woff2") format("woff2");
}
@font-face {
  font-weight: 400;
  font-family: Unbounded;
  font-style: normal;
  font-display: swap;
  src: url("/{{fonts}}/unbounded/Unbounded-Regular.woff2") format("woff2");
}
.about {
  position: relative;
  width: 100vw;
  min-height: auto;
  overflow: hidden;
  background: var(--grey-dark);
}
@media screen and (min-width: 1280px) {
  .about {
    height: 1000px;
    min-height: 100vh;
  }
}
.about::before {
  position: absolute;
  top: 0;
  left: 0;
  display: none;
  width: 100vw;
  height: 100%;
  background-image: url("../img/about__photo-01.png");
  background-repeat: no-repeat;
  background-size: contain;
  content: "";
}
@media screen and (min-width: 992px) {
  .about::before {
    display: block;
  }
}
.about__inner {
  position: relative;
  z-index: 1;
  padding: 40px 0;
}
@media screen and (min-width: 992px) {
  .about__inner {
    display: grid;
    grid-template-columns: repeat(12, 1fr);
    height: 100%;
    padding: 60px 0 0;
  }
}
@media screen and (min-width: 1280px) {
  .about__inner {
    padding: 100px 0 0;
  }
}
@media screen and (min-width: 992px) {
  .about__left, .about__right {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }
}
@media screen and (min-width: 992px) {
  .about__left {
    grid-column: 1/span 4;
    grid-template-columns: repeat(5, 1fr);
  }
}
.about__right {
  margin-top: 40px;
}
@media screen and (min-width: 992px) {
  .about__right {
    display: block;
    grid-column: 6/span 7;
    grid-template-columns: repeat(7, 1fr);
    margin-top: 0;
    padding-bottom: 160px;
  }
}
.about__section-title {
  position: relative;
  padding-right: 40px;
}
.about__section-title::before {
  position: absolute;
  right: 0;
  bottom: 0;
  width: 30px;
  height: 30px;
  background-image: url("../img/icon__arrow.svg");
  background-size: 30px;
  content: "";
}
@media screen and (min-width: 1280px) {
  .about__section-title::before {
    bottom: 5px;
    width: 40px;
    height: 40px;
    background-size: 40px;
  }
}
.about__block-title, .about__text {
  color: var(--white);
  font-weight: 300;
}
.about__block-title {
  margin: 0 0 16px;
  font-size: calc(22px + ( 32 - 22 ) * ( (100vw - 320px) / ( 1680 - 320) ));
  line-height: calc(28px + ( 36 - 28 ) * ( (100vw - 320px) / ( 1680 - 320) ));
}
.about__block-title--blue {
  background: var(--purple);
}
.about__container-text {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
}
@media screen and (min-width: 992px) {
  .about__container-text {
    margin-top: 16px;
  }
}
.about__text {
  width: 100%;
  margin: 0 0 8px;
}
.about__text:last-child {
  margin-bottom: 0;
}
.about__text div {
  color: transparent;
  font-weight: 400;
  font-size: calc(14px + ( 16 - 14 ) * ( (100vw - 320px) / ( 1680 - 320) ));
  line-height: calc(20px + ( 24 - 20 ) * ( (100vw - 320px) / ( 1680 - 320) ));
  background: linear-gradient(to right, var(--white) 50%, var(--grey-light) 50%);
  background-position-x: 100%;
  background-size: 200% 100%;
  background-clip: text;
}

.container {
  position: relative;
  width: calc(100% - 40px);
  margin: 0 auto;
}
@media screen and (min-width: 1280px) {
  .container {
    width: calc(100% - 172px);
    height: 100vh;
  }
}
@media screen and (min-width: 1680px) {
  .container {
    max-width: 1680px;
  }
}
.container::before {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 0;
  display: none;
  width: 100%;
  height: 100%;
  background-image: url("../img/section__lines.png");
  content: "";
}

.corners {
  display: none;
}
@media screen and (min-width: 992px) {
  .corners {
    display: flex;
    align-items: center;
    height: 100%;
  }
}
.corners__left {
  display: flex;
  flex-direction: column;
  gap: 360px;
  justify-content: space-between;
}
.corners__icon {
  width: 10px;
  height: 10px;
}
.corners__icon--bottom-left {
  transform: scale(1, -1);
}

.gallery {
  width: 100vw;
  height: 500px;
  min-height: auto;
  background: lightblue;
}
@media screen and (min-width: 1280px) {
  .gallery {
    height: 100%;
    min-height: auto;
  }
}
.gallery__container-photo {
  display: flex;
  width: 100%;
  height: 100%;
}
.gallery__container-photo__photo {
  width: 780px;
  height: 100%;
  object-fit: cover;
  aspect-ratio: 1/1;
}

.hero {
  overflow: hidden;
  background: var(--purple);
}
@media screen and (min-width: 1280px) {
  .hero {
    width: 100vw;
    height: 100vh;
  }
}
.hero__inner {
  position: relative;
  z-index: 5;
  padding: 40px 0;
}
@media screen and (min-width: 992px) {
  .hero__inner {
    display: grid;
    grid-template-columns: repeat(12, 1fr);
    height: 100%;
    padding: 60px 0 0;
  }
}
@media screen and (min-width: 1280px) {
  .hero__inner {
    padding: 100px 0 0;
  }
}
@media screen and (min-width: 992px) {
  .hero__left, .hero__right {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }
}
@media screen and (min-width: 992px) {
  .hero__left {
    grid-column: 1/span 5;
    grid-template-columns: repeat(7, 1fr);
    height: 100vh;
  }
}
@media screen and (min-width: 992px) {
  .hero__right {
    grid-column: 7/span 6;
    grid-template-columns: repeat(6, 1fr);
    height: 100vh;
  }
}
.hero__container-photo {
  display: flex;
  justify-content: flex-end;
  max-height: 50%;
  margin: 20px 0;
}
@media screen and (min-width: 1280px) {
  .hero__container-photo {
    margin: 0;
  }
}
.hero__photo {
  width: 65%;
  object-fit: contain;
}
@media screen and (min-width: 992px) {
  .hero__photo {
    width: 85%;
  }
}
.hero__container-quote {
  position: relative;
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
  width: 100%;
  margin: auto;
  padding-bottom: 40px;
}
@media screen and (min-width: 1280px) {
  .hero__container-quote {
    padding-bottom: 80px;
  }
}
.hero__svg-quote {
  flex-shrink: 0;
  width: 30px;
  height: 30px;
}
@media screen and (min-width: 1280px) {
  .hero__svg-quote {
    width: 48px;
    height: 48px;
  }
}
.hero__svg-quote--top {
  position: relative;
  top: -20px;
}
@media screen and (min-width: 1280px) {
  .hero__svg-quote--top {
    top: -40px;
  }
}
.hero__svg-quote--bottom {
  position: absolute;
  right: 0;
  bottom: 40px;
}
@media screen and (min-width: 1280px) {
  .hero__svg-quote--bottom {
    position: relative;
    bottom: -70px;
  }
}
.hero__text-quote, .hero__text-name {
  width: calc(100% - 30px);
  margin: 0;
  color: var(--white);
  font-weight: 300;
  line-height: calc(20px + ( 24 - 20 ) * ( (100vw - 320px) / ( 1680 - 320) ));
}
@media screen and (min-width: 1280px) {
  .hero__text-quote, .hero__text-name {
    width: 100%;
  }
}
.hero__text-quote {
  margin-bottom: 16px;
  font-size: calc(14px + ( 18 - 14 ) * ( (100vw - 320px) / ( 1680 - 320) ));
}
.hero__text-name {
  font-size: calc(14px + ( 16 - 14 ) * ( (100vw - 320px) / ( 1680 - 320) ));
  text-align: right;
}
.hero__title-block, .hero__text {
  color: var(--white);
  font-weight: 300;
  font-style: normal;
}
.hero__title-block {
  margin: 0 0 16px;
  font-size: calc(22px + ( 32 - 22 ) * ( (100vw - 320px) / ( 1680 - 320) ));
  line-height: calc(28px + ( 36 - 28 ) * ( (100vw - 320px) / ( 1680 - 320) ));
}
.hero__text {
  margin: 0 0 10px;
  font-size: calc(14px + ( 16 - 14 ) * ( (100vw - 320px) / ( 1680 - 320) ));
  line-height: calc(20px + ( 24 - 20 ) * ( (100vw - 320px) / ( 1680 - 320) ));
}
.hero__block-stamps {
  display: none;
  gap: 0 50px;
  align-items: flex-end;
}
@media screen and (min-width: 992px) {
  .hero__block-stamps {
    display: flex;
  }
}
.hero__photo-stamps:first-child {
  transform: rotate(15deg);
}
.hero__photo-stamps:last-child {
  transform: rotate(-15deg);
}

.lines {
  display: none;
}
@media screen and (min-width: 1280px) {
  .lines {
    display: block;
  }
}
.lines__inner, .lines__line {
  position: fixed;
  top: 50%;
  left: 50%;
  z-index: 1;
  width: 100vh;
  height: 100vh;
  transform: translate(-50%, -50%);
  pointer-events: none;
}
.lines__inner::before, .lines__inner::after, .lines__line::before, .lines__line::after {
  position: absolute;
  background: rgba(255, 255, 255, 0.12);
  content: "";
}
.lines__inner {
  border: 1px solid rgba(255, 255, 255, 0.12);
  border-radius: 50%;
}
.lines__inner::before {
  top: 0;
  left: 0;
  width: 1px;
  height: 100%;
}
.lines__inner::after {
  top: 0;
  right: 0;
  width: 1px;
  height: 100%;
}
.lines__line::before {
  top: 0;
  left: 50%;
  width: 1px;
  height: 100%;
}
.lines__line::after {
  top: 50%;
  left: -100%;
  width: 200vw;
  height: 1px;
}

.news {
  width: 100vw;
  height: 100%;
  min-height: auto;
  background: lightgrey;
}
@media screen and (min-width: 1280px) {
  .news {
    height: 100%;
  }
}

.planet {
  width: 100vw;
  height: 500px;
  min-height: auto;
  background: var(--white);
}
@media screen and (min-width: 1280px) {
  .planet {
    height: 100%;
    min-height: 100vh;
  }
}

.scroll-indicator {
  position: fixed;
  top: 0;
  z-index: 1;
  width: 100%;
}
.scroll-indicator__inner {
  width: 100%;
  height: 4px;
}
.scroll-indicator__progress {
  width: 0;
  height: 4px;
  background: var(--black);
}

.scroll-trigger {
  overflow-x: hidden;
}
.scroll-trigger__inner, .scroll-trigger__animation {
  display: flex;
  min-height: 100vh;
}
.scroll-trigger__inner--right, .scroll-trigger__animation--right {
  display: block;
  overflow: hidden;
}
@media screen and (min-width: 1280px) {
  .scroll-trigger__inner--right, .scroll-trigger__animation--right {
    display: flex;
    overflow: inherit;
  }
}

.section-title {
  margin: 0;
  color: var(--white);
  font-size: calc(26px + ( 40 - 26 ) * ( (100vw - 320px) / ( 1680 - 320) ));
  font-family: var(--font-unbounded);
  line-height: calc(26px + ( 40 - 26 ) * ( (100vw - 320px) / ( 1680 - 320) ));
  text-transform: uppercase;
}

.technologies {
  width: 100vw;
  height: 500px;
  min-height: auto;
  background: var(--purple-light);
}
@media screen and (min-width: 1280px) {
  .technologies {
    height: 100%;
  }
}

